import { renderBooks } from './renderBooks';
import { modalShow } from './modal';
import Pagination from 'tui-pagination';
import 'tui-pagination/dist/tui-pagination.css';

import Pagination from 'tui-pagination';
import 'tui-pagination/dist/tui-pagination.css';

const LIBRARY_LIST = 'library';
const localArr = JSON.parse(localStorage.getItem(LIBRARY_LIST));
const libraryEl = document.querySelector('.loc-library__list');
libraryEl.insertAdjacentHTML('afterbegin', `<div id="pagination" class="tui-pagination"></div>`);
const container = document.getElementById('pagination');

if (localArr !== null) {
  libraryEl.innerHTML = renderBooks(localArr, 'library', false, false);
}

function renderOptionsForPagination(localArr) {
  let itemsPerPage = 0;
  if (window.innerWidth < 768) {
    itemsPerPage = 6;
  } else if (window.innerWidth >= 768 && window.innerWidth < 1280) {
    itemsPerPage = 14;
  } else if (window.innerWidth >= 1280) {
    itemsPerPage = 21;
  }

  return {
    totalItems: localArr.length,
    itemsPerPage: itemsPerPage,
    visiblePages: 6,
    page: 1,
    centerAlign: false,
    firstItemClassName: 'tui-first-child',
    lastItemClassName: 'tui-last-child',
    template: {
      page: '<a href="#" class="tui-page-btn">{{page}}</a>',
      currentPage: '<strong class="tui-page-btn tui-is-selected">{{page}}</strong>',
      moveButton:
        '<a href="#" class="tui-page-btn tui-{{type}}">' +
        '<span class="tui-ico-{{type}}">{{type}}</span>' +
        '</a>',
      disabledMoveButton:
        '<span class="tui-page-btn tui-is-disabled tui-{{type}}">' +
        '<span class="tui-ico-{{type}}">{{type}}</span>' +
        '</span>',
      moreButton:
        '<a href="#" class="tui-page-btn tui-{{type}}-is-ellip">' +
        '<span class="tui-ico-ellip">...</span>' +
        '</a>',
    },
  };
}

libraryEl.addEventListener('click', modalShow);
const pagination = new Pagination(container, renderOptionsForPagination(localArr));
